@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.contenedor-seccion-wrap {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.contenedor-img-header .img-header {
  height: 6rem;
  width: 7rem;
}

.navegacion1 .nav-1 a {
  color: #727176;
  font-family: "BioFemme Comfortaa", Sans-serif;
  margin-right: 0.5rem;
  font-weight: normal;
  font-size: 18px;
}
.navegacion1 .nav-1 .tienda-p {
  color: #C9777D;
}
.navegacion1 .nav-1 .productos-p {
  text-decoration: none;
}
.navegacion1 .nav-1 a:hover {
  color: #C9777D;
  text-decoration: underline;
}

.nav-2 {
  margin: 0;
  display: flex;
  align-items: center;
}
.nav-2 .icono {
  height: 2rem;
  width: 2rem;
  margin: 0;
}
.nav-2 .texto div {
  color: #727176;
  font-family: "BioFemme Comfortaa", Sans-serif;
}

.Biofemme-p a {
  color: #727176;
  font-family: "BioFemme Comfortaa", Sans-serif;
  margin-right: 0.5rem;
  font-weight: normal;
  font-size: 18px;
  text-decoration: none;
}
.Biofemme-p a:hover {
  color: #C9777D;
  text-decoration: underline;
}

/** Media Queries **/
.contenedor-cosmetica {
  margin-top: 3rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenedor-cosmetica .contenedor-img-cosmetica {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-cosmetica .contenedor-img-cosmetica .img-cosmetica img {
  width: 40rem;
  height: 22rem;
  border-radius: 0.7rem;
}

.nombre-ing-produ {
  text-transform: uppercase;
}

.elementor-seccion-products {
  margin-top: 2rem;
}
.elementor-seccion-products .contenido-seccion-products .contenido-img-products {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 1024px) {
  .elementor-seccion-products .contenido-seccion-products .contenido-img-products {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.elementor-seccion-products .contenido-seccion-products .contenido-img-products .img-productos {
  margin-right: 5rem;
}
.elementor-seccion-products .contenido-seccion-products .contenido-img-products .img-productos .img-perfume {
  height: 20rem;
  width: 15rem;
}
.elementor-seccion-products .contenido-seccion-products .contenido-img-products .img-productos:hover {
  box-shadow: 4px 11px 6px 6px rgba(205, 205, 205, 0.76);
  -webkit-box-shadow: 4px 11px 6px 6px rgba(205, 205, 205, 0.76);
  -moz-box-shadow: 4px 11px 6px 6px rgba(205, 205, 205, 0.76);
  cursor: pointer;
}
.elementor-seccion-products .btn-ver {
  margin-top: 4rem;
  text-align: center;
}
.elementor-seccion-products .btn-ver .btn {
  color: #FFFFFF;
  border-style: solid;
  border-width: 1px;
  border-color: #C9777D;
  border-radius: 50px;
  background-color: #C9777D;
  text-decoration: none;
  padding: 15px 40px;
  font-weight: bold;
  font-size: 1rem;
  text-transform: uppercase;
}
.elementor-seccion-products .btn-ver .btn:hover {
  cursor: pointer;
  background-color: #FFF0F4;
  color: #C9777D;
}

.titulo-seccion-products {
  padding: 0 4rem;
}
.titulo-seccion-products p {
  text-transform: uppercase;
  font-family: "BioFemme Comfortaa", Sans-serif;
  letter-spacing: 1.5px;
  margin: 0;
}
.titulo-seccion-products samp p {
  color: #C9777D;
  font-size: 35px;
  font-weight: 600;
}

.contenido-descripcion-products {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FFF0F4;
}
.contenido-descripcion-products .texto-contenido-products p {
  margin: 0;
  font-family: "BioFemme Comfortaa", Sans-serif;
  color: inherit;
  padding: 3px 10px;
}
.contenido-descripcion-products .icon-carrito img {
  height: 2rem;
  width: 2rem;
}

.imagen-contacto {
  width: 100%;
  margin-top: 4rem;
  background-image: url("https://www.compras.biofemme.com.ec/wp-content/uploads/2020/11/bg-movil-sus.jpg");
  background-position: 23% 0px;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contacto h2 {
  color: #C9777D;
  font-family: "BioFemme Comfortaa", Sans-serif;
  font-size: 50px;
  font-weight: bold;
}
.contacto p {
  color: #727176;
  font-family: "Futura", Sans-serif;
  font-size: 25px;
  font-weight: bold;
}

.formulario-contacto .form {
  display: flex;
}
.formulario-contacto .form input[type=email] {
  background-color: #ffffff;
  border-width: 0px 0px 0px 0px;
  border-radius: 50px 0px 0px 50px;
  font-size: 18px;
  min-height: 30px;
  width: 100%;
  padding: 7px 20px;
}
.formulario-contacto .form input[type=submit] {
  color: #FFFFFF;
  border-color: #C9777D;
  background-color: #C9777D;
  font-family: "Futura", Sans-serif;
  font-weight: 500;
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  border-radius: 0px 50px 50px 0px;
  padding: 10px 30px 10px 30px;
}
.formulario-contacto .form input[type=submit]:hover {
  cursor: pointer;
  background-color: #FFF0F4;
  color: #C9777D;
}

.contenido-footer {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .contenido-footer {
    display: flex;
    justify-content: space-evenly;
  }
  .contenido-footer .oculta {
    display: none;
  }
}
.contenido-footer .contenido1 p {
  color: #C9777D;
  text-transform: uppercase;
}

.img-footer {
  height: 6rem;
  width: 7rem;
}

.title-footer {
  text-transform: uppercase;
  font-size: 1.3rem;
  font-family: "BioFemme Comfortaa", Sans-serif;
  margin: 0;
}

.enlaces-footer {
  display: inline-grid;
}
.enlaces-footer a {
  text-decoration: none;
  color: #000000;
  margin-top: 10px;
}

.img-pagos {
  margin-top: 1.5rem;
}
.img-pagos img {
  margin-right: 2rem;
}

.fa {
  padding: 20px;
  font-size: 30px;
  width: 20px;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
  background-color: #C9777D;
  color: #FFFFFF;
}

.derechos {
  display: none;
}

@media (min-width: 768px) {
  .derechos {
    width: 80%;
    display: block;
  }
}
.contenido-derechos {
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-derechos {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
}